import React, { Component } from 'react';
import './WeatherDisplay.css'
function kelvin_to_farenheight(temp){
        return temp*9/5-459.67;
}
class WeatherDisplay extends React.Component{
     
        constructor(props){
                super(props);

                //Map the returned icon code to this app's custom icon file names
                let imageMap = {
                                _01d: "Sun",
                                _01n: "Night_Sun",
                                _02d: "Sun_Cloud_Day",
                                _02n: "Sun_Cloud_Night",
                                _03d: "Cloud",
                                _03n: "Cloud_Night",
                                _04d: "Cloud_Broken",
                                _04n: "Cloud_Broken",
                                _09d: "Rain_Day",
                                _09n: "Rain_Night",
                                _10d: "Rain_Day",
                                _10n: "Rain_Night",
                                _11d: "Storm_Day",
                                _11n: "Storm_Night",
                                _13d: "Snow",
                                _13n: "Snow",
                                _50d: "Mist",
                                _50n: "Mist_Night"
                                };
                this.state = {imageMap: imageMap};
        }

      

        render(){
                var icon = "_"+this.props.icon;
                var image = `/img/${this.state.imageMap[icon]}.svg`;
                var ret = (
                        <div className="wDisplay">
                                <h3>{parseFloat(kelvin_to_farenheight(this.props.temp).toFixed(2))}°</h3>
                                <img className="weatherImage" src={`${image}`} alt={`${this.props.weather}`}/>
                                <h3>{this.props.description}</h3>
                        </div>
                );
                //Render the proper image or return null if no weather is set
                return this.props.weather == null ? null : ret;

                //return this.props.weather == null ? null : <img src={`${image}`} style={style} alt={`${this.props.weather}`}/> ;
        }
}


export default WeatherDisplay;



