import React, { Component } from 'react';

import './weatherContainer.css';
import WeatherDisplay from './WeatherDisplay/WeatherDisplay'

class WeatherContainer extends React.Component {
     
    constructor(props){
        super(props);
        this.state = {temperature: null, weather: null, inputValue: '', temp: null, icon: null };
        this.zipLookUp = this.zipLookUp.bind(this);
    }

    zipLookUp(e){
        fetch(`//api.openweathermap.org/data/2.5/weather?zip=${this.state.inputValue}&APPID=${process.env.REACT_APP_API_KEY}`).then ( response => response.json() )
        .then (data => {
            console.log(data);
            this.setState({weather: data.weather[0].main,
                        temp: data.main.temp,
                        icon: data.weather[0].icon,
                        description: data.weather[0].description
                    })
        })
        .catch(console.log);
    }

    updateInputValue(evt){
        this.setState({
            inputValue: evt.target.value
        });
    }

    render(){
        console.log(this.state.icon)
        return(
            <div className="root">
                <h2>Just enter your zip code and press go!</h2>
                <div className = "contentContainer">
                    <div className = "zipContainer">
                        <input className="zipInput" value={this.state.inputValue} onChange={evt => this.updateInputValue(evt)}></input>
                        <button className = "zipButton" value="Go!" onClick={this.zipLookUp}>Go!</button>
                    </div>
                    <WeatherDisplay weather={this.state.weather} icon={this.state.icon} temp={this.state.temp} description={this.state.description}/>
                </div>
            </div>
        );
    }
}

export default WeatherContainer;



